:root {
	--menu-active-color: #a66289;
	--menu-active-background: #f7f7f7;
}

@font-face {
	font-family: titles;
	src: url("../fonts/ArchitectsDaughter-Regular.ttf");
}

@font-face {
	font-family: notes;
	src: url("../fonts/PTSans-Regular.ttf");
}

* {
	box-sizing: border-box;
}

a {
	text-decoration: none;
	color: inherit;
	padding: 5px 10px 15px 10px;
}

.css-10hburv-MuiTypography-root {
	font-family: inherit;
	font-weight: inherit;
	font-size: inherit;
	line-height: inherit;
	letter-spacing: inherit;
	display: inherit;
}

.activeMenu {
	background: var(--menu-active-background);
	color: var(--menu-active-color);
	border-radius: 10px 10px 0px 0px;
	position: relative;
	height: 30px;
	width: 60px;
}

.sharedNote {
	background: linear-gradient(135deg, #fbf6fc, #e7d8e7);
	border-radius: 5px;
	box-shadow: 1px 2px 3px #747474;
}
